'use client';

import React, { useState, useEffect, useRef } from 'react';
import Icon from '@/components/ui/AppIcon';

interface Stat {
  id: number;
  value: number;
  suffix: string;
  label: string;
  icon: string;
  description: string;
}

interface StatsSectionProps {
  className?: string;
}

const StatsSection = ({ className = '' }: StatsSectionProps) => {
  const [isHydrated, setIsHydrated] = useState(false);
  const [isVisible, setIsVisible] = useState(false);
  const [counts, setCounts] = useState<Record<number, number>>({});
  const sectionRef = useRef<HTMLDivElement>(null);

  const stats: Stat[] = [
    {
      id: 1,
      value: 12,
      suffix: "+",
      label: "Años de Experiencia",
      icon: "CalendarIcon",
      description: "Transformando negocios desde 2014"
    },
    {
      id: 2,
      value: 250,
      suffix: "+",
      label: "Proyectos Completados",
      icon: "CheckBadgeIcon",
      description: "Soluciones entregadas con éxito"
    },
    {
      id: 3,
      value: 98,
      suffix: "%",
      label: "Satisfacción del Cliente",
      icon: "FaceSmileIcon",
      description: "Índice de retención y recomendación"
    },
    {
      id: 4,
      value: 45,
      suffix: "+",
      label: "Expertos Certificados",
      icon: "AcademicCapIcon",
      description: "Equipo multidisciplinario especializado"
    }
  ];

  useEffect(() => {
    setIsHydrated(true);
  }, []);

  useEffect(() => {
    if (!isHydrated) return;

    const observer = new IntersectionObserver(
      (entries) => {
        if (entries[0].isIntersecting) {
          setIsVisible(true);
        }
      },
      { threshold: 0.3 }
    );

    if (sectionRef.current) {
      observer.observe(sectionRef.current);
    }

    return () => observer.disconnect();
  }, [isHydrated]);

  useEffect(() => {
    if (!isVisible || !isHydrated) return;

    const duration = 2000;
    const steps = 60;
    const stepDuration = duration / steps;

    stats.forEach((stat) => {
      let currentStep = 0;
      const increment = stat.value / steps;

      const timer = setInterval(() => {
        currentStep++;
        const newValue = Math.min(Math.floor(increment * currentStep), stat.value);
        setCounts((prev) => ({ ...prev, [stat.id]: newValue }));

        if (currentStep >= steps) {
          clearInterval(timer);
        }
      }, stepDuration);
    });
  }, [isVisible, isHydrated]);

  return (
    <section data-component-id="src/app/homepage/components/StatsSection.tsx:107:4" data-component-path="src/app/homepage/components/StatsSection.tsx" data-component-line="107" data-component-end-line="141" data-component-file="StatsSection.tsx" data-component-name="section" data-component-content="%7B%22elementName%22%3A%22section%22%7D" ref={sectionRef} className={`py-16 lg:py-24 bg-muted ${className}`}>
      <div data-component-id="src/app/homepage/components/StatsSection.tsx:108:6" data-component-path="src/app/homepage/components/StatsSection.tsx" data-component-line="108" data-component-end-line="140" data-component-file="StatsSection.tsx" data-component-name="div" data-component-content="%7B%22elementName%22%3A%22div%22%2C%22className%22%3A%22container%20mx-auto%20px-4%22%7D" className="container mx-auto px-4">
        <div data-component-id="src/app/homepage/components/StatsSection.tsx:109:8" data-component-path="src/app/homepage/components/StatsSection.tsx" data-component-line="109" data-component-end-line="116" data-component-file="StatsSection.tsx" data-component-name="div" data-component-content="%7B%22elementName%22%3A%22div%22%2C%22className%22%3A%22text-center%20mb-12%22%7D" className="text-center mb-12">
          <h2 data-component-id="src/app/homepage/components/StatsSection.tsx:110:10" data-component-path="src/app/homepage/components/StatsSection.tsx" data-component-line="110" data-component-end-line="112" data-component-file="StatsSection.tsx" data-component-name="h2" data-component-content="%7B%22elementName%22%3A%22h2%22%2C%22className%22%3A%22text-3xl%20lg%3Atext-4xl%20font-heading%20font-bold%20text-foreground%20mb-4%22%2C%22textContent%22%3A%22Resultados%20que%20Hablan%20por%20S%C3%AD%20Mismos%22%7D" className="text-3xl lg:text-4xl font-heading font-bold text-foreground mb-4">
            Resultados que Hablan por Sí Mismos
          </h2>
          <p data-component-id="src/app/homepage/components/StatsSection.tsx:113:10" data-component-path="src/app/homepage/components/StatsSection.tsx" data-component-line="113" data-component-end-line="115" data-component-file="StatsSection.tsx" data-component-name="p" data-component-content="%7B%22elementName%22%3A%22p%22%2C%22className%22%3A%22text-lg%20text-muted-foreground%20max-w-2xl%20mx-auto%22%2C%22textContent%22%3A%22M%C3%A1s%20de%20una%20d%C3%A9cada%20construyendo%20soluciones%20tecnol%C3%B3gicas%20que%20generan%20impacto%20real%20en%20organizaciones%20de%20todos%20los%20tama%C3%B1os%22%7D" className="text-lg text-muted-foreground max-w-2xl mx-auto">
            Más de una década construyendo soluciones tecnológicas que generan impacto real en organizaciones de todos los tamaños
          </p>
        </div>

        <div data-component-id="src/app/homepage/components/StatsSection.tsx:118:8" data-component-path="src/app/homepage/components/StatsSection.tsx" data-component-line="118" data-component-end-line="139" data-component-file="StatsSection.tsx" data-component-name="div" data-component-content="%7B%22elementName%22%3A%22div%22%2C%22className%22%3A%22grid%20grid-cols-1%20sm%3Agrid-cols-2%20lg%3Agrid-cols-4%20gap-8%22%7D" className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-8">
          {stats.map((stat) => (
            <div data-component-id="src/app/homepage/components/StatsSection.tsx:120:12" data-component-path="src/app/homepage/components/StatsSection.tsx" data-component-line="120" data-component-end-line="137" data-component-file="StatsSection.tsx" data-component-name="div" data-component-content="%7B%22elementName%22%3A%22div%22%2C%22className%22%3A%22bg-card%20rounded-lg%20p-8%20text-center%20shadow-sm%20hover%3Ashadow-brand%20transition-all%20duration-300%20hover%3Ascale-105%22%7D"
              key={stat.id}
              className="bg-card rounded-lg p-8 text-center shadow-sm hover:shadow-brand transition-all duration-300 hover:scale-105"
            >
              <div data-component-id="src/app/homepage/components/StatsSection.tsx:124:14" data-component-path="src/app/homepage/components/StatsSection.tsx" data-component-line="124" data-component-end-line="126" data-component-file="StatsSection.tsx" data-component-name="div" data-component-content="%7B%22elementName%22%3A%22div%22%2C%22className%22%3A%22inline-flex%20items-center%20justify-center%20w-16%20h-16%20bg-primary%2F10%20rounded-full%20mb-4%22%7D" className="inline-flex items-center justify-center w-16 h-16 bg-primary/10 rounded-full mb-4">
                <Icon data-component-id="src/app/homepage/components/StatsSection.tsx:125:16" data-component-path="src/app/homepage/components/StatsSection.tsx" data-component-line="125" data-component-end-line="125" data-component-file="StatsSection.tsx" data-component-name="Icon" data-component-content="%7B%22elementName%22%3A%22Icon%22%2C%22className%22%3A%22text-primary%22%7D" name={stat.icon as any} size={32} variant="outline" className="text-primary" />
              </div>
              <div data-component-id="src/app/homepage/components/StatsSection.tsx:127:14" data-component-path="src/app/homepage/components/StatsSection.tsx" data-component-line="127" data-component-end-line="130" data-component-file="StatsSection.tsx" data-component-name="div" data-component-content="%7B%22elementName%22%3A%22div%22%2C%22className%22%3A%22text-4xl%20lg%3Atext-5xl%20font-heading%20font-bold%20text-primary%20mb-2%22%7D" className="text-4xl lg:text-5xl font-heading font-bold text-primary mb-2">
                {isHydrated ? counts[stat.id] || 0 : stat.value}
                <span data-component-id="src/app/homepage/components/StatsSection.tsx:129:16" data-component-path="src/app/homepage/components/StatsSection.tsx" data-component-line="129" data-component-end-line="129" data-component-file="StatsSection.tsx" data-component-name="span" data-component-content="%7B%22elementName%22%3A%22span%22%2C%22className%22%3A%22text-accent%22%7D" className="text-accent">{stat.suffix}</span>
              </div>
              <h3 data-component-id="src/app/homepage/components/StatsSection.tsx:131:14" data-component-path="src/app/homepage/components/StatsSection.tsx" data-component-line="131" data-component-end-line="133" data-component-file="StatsSection.tsx" data-component-name="h3" data-component-content="%7B%22elementName%22%3A%22h3%22%2C%22className%22%3A%22text-lg%20font-heading%20font-semibold%20text-foreground%20mb-2%22%7D" className="text-lg font-heading font-semibold text-foreground mb-2">
                {stat.label}
              </h3>
              <p data-component-id="src/app/homepage/components/StatsSection.tsx:134:14" data-component-path="src/app/homepage/components/StatsSection.tsx" data-component-line="134" data-component-end-line="136" data-component-file="StatsSection.tsx" data-component-name="p" data-component-content="%7B%22elementName%22%3A%22p%22%2C%22className%22%3A%22text-sm%20text-muted-foreground%22%7D" className="text-sm text-muted-foreground">
                {stat.description}
              </p>
            </div>
          ))}
        </div>
      </div>
    </section>
  );
};

export default StatsSection;